.c-inpage-navigation-wrapper {
    position: fixed;
	left: 0;
	height: 100%;
	top: 0;
	padding: 30px;
	display: flex;
	align-items: center;
}

.c-inpage-navigation {
    &__list {
	    text-align: center;
    }

    &__list-item {
		margin: 6px 0;
    }

    &__segment {
	    display: block;
	    width: 1px;
	    height: 40px;
	    background-color: transparentize($white, 0.8);
	    position: relative;
    }

    &__segment-line {
	    display: block;
	    position: absolute;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;
	    background-color: $white;
	    transform: scaleY(0);
	    transform-origin: top;
    }
}
